import React from 'react';
import { HashRouter, Link } from 'react-router-dom';
import SpaceShip from '../SpaceShip/SpaceShip';

function Gallery() {

    const listString = localStorage.getItem('shipList');
    const list = JSON.parse(listString).reverse();
    console.log(listString);

    return (
        <div className="container">
            <HashRouter>

                <Link className="title title--link" to="/">HANGAR</Link>

            </HashRouter>

            <div className="gallery">
                <button className="gallery__btn"></button>               
                <button className="gallery__btn gallery__btn--right"></button>

                {list.map((e) => 
                
                    <SpaceShip key={e.id}
                    shape={e.shape}
                    primaryColor={e.primaryColor}
                    secondaryColor={e.secondaryColor}
                    fireActivated={e.fireActivated}
                    fireColor={e.fireColor}
                    name={e.name}
                    background={e.background}/>
                )}
            </div>
            
        </div>
    );
}

export default Gallery;