import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { v4 as uuidv4 } from 'uuid';

function StepThree(props) {

    const [ focused, setFocused ] = React.useState(false);

    function handleName(event) {
        if(event.target.value.length <= 7) {
            props.setName(event.target.value);
        }           
    }

    function handleFocus(event) {
        setFocused(true);
    }

    function handleBlur(event) {
        if(!event.target.value) {
            setFocused(false);
        }
    }

    function handleChangeBg(event) {
        props.setBackground(parseInt(event.target.closest('div').getAttribute('data-bg')));
    }

    function handleFinishButton() {
        const newId = uuidv4();
        props.setId(newId);
        props.handleFinish(newId);     
    }

    if(props.name && !focused) {
        setFocused(true);
    }

    return (
        <div className="creator__step"> 
            <div className={focused? 'creator__textInput--focused creator__textInput' : 'creator__textInput'}>
                <label htmlFor="shipName">Nombre de la nave</label>
                <input id="shipName" type="text" onFocus={handleFocus} onChange={handleName} 
                onBlur={handleBlur} value={props.name}/>
            </div>

            <div className="creator__option">
                <h3>Fondo</h3>

                <div className='creator__buttonsContainer'>
                    <div data-bg={1} onClick={handleChangeBg} 
                    className={props.background === 1 ? 'creator__btn creator__btn--selectedBg' : 'creator__btn creator__btn--bg'}>
                        <img src='/resources/bg1Icon.png' alt=''/>
                    </div>

                    <div data-bg={2} onClick={handleChangeBg} 
                    className={props.background === 2 ? 'creator__btn creator__btn--selectedBg' : 'creator__btn creator__btn--bg'}>
                        <img src='/resources/bg2Icon.png' alt=''/>
                    </div>

                    <div data-bg={3} onClick={handleChangeBg} 
                    className={props.background === 3 ? 'creator__btn creator__btn--selectedBg' : 'creator__btn creator__btn--bg'}>
                        <img src='/resources/bg3Icon.png' alt=''/>
                    </div>

                    <div data-bg={4} onClick={handleChangeBg} 
                    className={props.background === 4 ? 'creator__btn creator__btn--selectedBg' : 'creator__btn creator__btn--bg'}>
                        <img src='/resources/bg4Icon.png' alt=''/>
                    </div>
                </div>
            </div>

            <Link className="creator__end" to="/" onClick={handleFinishButton}>
                <Button variant="contained" color="primary">Terminar nave</Button>
            </Link>
        </div>
    );
}

StepThree.propTypes = {
    name: PropTypes.string,
    setName: PropTypes.func,
    background: PropTypes.number,
    setBackground: PropTypes.func,
    id: PropTypes.string,
    setId: PropTypes.func,
    handleFinish: PropTypes.func,
}

export default StepThree;